@use "base/variables" as v;

/*=============== BREAKPOINTS ===============*/

/* For small devices */
@media screen and (max-width: 340px) {
  .container {
    margin-inline: 1rem;
  }

  .nav__link {
    padding-inline: 1rem;
  }
}

/* For large devices */
@media screen and (min-width: 1118px) {
  .container {
    margin-inline: auto;
  }

  .nav {
    height: calc(3.5rem + 2rem);
    display: flex;
    justify-content: space-between;

    &__toggle {
      display: none;
    }
    &__list {
      height: 100%;
      display: flex;
      column-gap: 3rem;
    }
    &__link {
      height: 100%;
      padding: 0;
      justify-content: initial;
      column-gap: 0.25rem;

      &:hover {
        background-color: transparent;
      }
    }
  }

  .dropdown {
    &__item,
    &__subitem {
      position: relative;
    }
    &__menu,
    &__submenu {
      max-height: initial;
      overflow: initial;
      position: absolute;
      left: 0;
      top: calc((3.5rem) + 6rem);
      opacity: 0;
      pointer-events: none;
      transition: opacity 0.3s, top 0.3s;
    }
    &__link,
    &__sublink {
      padding-inline: 1rem 3.5rem;
    }
    &__subitem &__link {
      padding-inline: 1rem;
    }
    &__submenu {
      position: absolute;
      left: 100%;
      top: 0.5rem;
    }
    // Show dropdown menu
    &__item:hover &__menu {
      opacity: 1;
      top: calc(3.5rem + 5.5rem);
      pointer-events: initial;
      transition: top 0.3s;
    }
    // Show dropdown submenu
    &__subitem:hover > &__submenu {
      opacity: 1;
      top: 0;
      pointer-events: initial;
      transition: top 0.3s;
    }
  }
}
